@font-face {
  font-family: "VAGRoundedSTDBlack";
  src: url(/odin-jollibee-clone/static/media/VAGRoundedStdBlack.69db9bbd.otf);
}
@font-face {
  font-family: "VAGRoundedSTDThin";
  src: url(/odin-jollibee-clone/static/media/VAGRoundedStdThin.6a9c01ef.otf);
}
@font-face {
  font-family: "VAGRoundedSTDLight";
  src: url(/odin-jollibee-clone/static/media/VAGRoundedStdLight.21b0e9c9.otf);
}

:root {
  --clr-primary: #d6001c;
  --clr-secondary: #fa8003;
  --clr-text: #212529;
  --clr-accent: #009ca6;
  --clr-white: #e3e3e3;
  --clr-bg-secondary: #ffeab8;
  --clr-hover: #ff620f;

  --font-primary: "VAGRoundedSTDLight";
  --font-thin: "VAGRoundedSTDThin";
  --font-bold: "VAGRoundedSTDBlack";
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: inherit;
  -webkit-user-select: none;
          user-select: none;
}

body {
  font-family: "VAGRoundedSTDBlack";
  font-family: var(--font-bold);
  color: #212529;
  color: var(--clr-text);
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
  background-color: var(--clr-primary);
  border-bottom: 5px solid var(--clr-secondary);
  width: 100%;
  z-index: 10;

  position: -webkit-sticky;

  position: sticky;
  top: 0;
}

.header__container {
  margin: 0 auto;
  max-width: 1250px;
  color: white;
  padding: 0.5em 1em;

  display: flex;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 1.5em;
  gap: 1.5em;
}

.logo {
  margin-right: auto;
  cursor: pointer;
}

.nav-list {
  font-family: var(--font-bold);
  font-size: 1.2em;

  display: flex;
  align-items: center;
  grid-gap: 2em;
  gap: 2em;

  -webkit-padding-start: 0;

          padding-inline-start: 0;
}

.nav-list > * {
  cursor: pointer;
  padding: 1em 0;
  -webkit-user-select: none;
          user-select: none;

  text-decoration: none;
  list-style-type: none;
  color: white;
}

.nav-list > * .arrow {
  fill: white;
  scale: 0.8;
}

.nav-list > *:hover {
  color: var(--clr-white);
}

.nav-list li.menu:hover svg.arrow,
.nav-list li.promotions:hover svg.arrow {
  /* fill: var(--clr-white); */
  fill: var(--clr-white);
}

.nav-list .menu,
.nav-list .promotions {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 0.3em;
  gap: 0.3em;

  position: relative;
}

.menu-drop-down-list,
.promotions-drop-down-list {
  position: absolute;
  top: 3em;
  background-color: white;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  padding: 1em 0;
}

.menu-drop-down-list li,
.promotions-drop-down-list li {
  color: black;
  list-style-type: none;
  text-align: center;
  font-family: var(--font-primary);
  -webkit-animation: menu 500ms ease-in-out forwards;
          animation: menu 500ms ease-in-out forwards;

  width: 20ch;
  padding: 0.5em 0;

  display: none;
}

.nav-list .menu:hover .menu-drop-down-list li,
.nav-list .promotions:hover .promotions-drop-down-list li {
  display: block;
}

@-webkit-keyframes menu {
  0% {
    opacity: 0;
    scale: 1.2;
  }

  100% {
    opacity: 1;
    scale: 1;
  }
}

@keyframes menu {
  0% {
    opacity: 0;
    scale: 1.2;
  }

  100% {
    opacity: 1;
    scale: 1;
  }
}

.menu-drop-down-list li:hover,
.promotions-drop-down-list li:hover {
  color: rgb(0 0 0 / 0.8);
  text-decoration: underline;
}

.order-btn {
  padding: 0.6em 1.2em;
  font-size: 1.3em;
  font-family: var(--font-bold);
  cursor: pointer;

  border-radius: 999em;
  border: none;
  background-color: var(--clr-secondary);
  color: white;

  box-shadow: 0 3px 4px 1px rgb(0 0 0 / 0.3);
  -webkit-user-select: none;
          user-select: none;
}

.order-btn:hover {
  background-color: var(--clr-hover);
}

/*  GENERAL */

.container {
  max-width: 1250px;
  margin: 0 auto;
}

/* HERO SECTION */
.heroSection {
  height: 500px;
  width: 100%;

  background-image: url(/odin-jollibee-clone/static/media/hero_banner.22ebcad6.jpeg);
  background-position: center;
  background-size: cover;
}

.heroSection__container {
  display: grid;
  justify-items: center;
  align-items: center;
  grid-template-columns: 1fr 1fr;

  height: 100%;
}

.slogan {
  display: grid;
  grid-gap: 2em;
  gap: 2em;
  justify-items: center;
  max-width: calc(1250px / 2);
  -webkit-user-select: none;
          user-select: none;
}

.slogan > *:not(button) {
  color: white;
  font-family: var(--font-bold);
  text-align: center;
}

.slogan h1 {
  font-size: 3em;
}

.slogan h3 {
  font-size: 1.8em;
}

.slogan .order-btn {
  width: -webkit-max-content;
  width: max-content;
  scale: 1.2;
}

/* CALL TO ACTION SECTION (CTA) */

.CTA__container {
  margin: 0 auto;

  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding: 2.5em;
}

.CTA__container > * {
  display: grid;
  justify-items: center;
  grid-gap: 1em;
  gap: 1em;

  cursor: pointer;
}

.order:hover,
.download:hover,
.call:hover {
  scale: 1.05;
}

.CTA__container > * > h3 {
  font-size: 1.8em;
  font-family: var(--font-bold);
  letter-spacing: 2px;
  color: var(--clr-text);
  -webkit-user-select: none;
          user-select: none;
  text-align: center;
}

.img {
  width: 150px;
  height: 150px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.order-img {
  background-image: url(/odin-jollibee-clone/static/media/order_pickup.b7167349.png);
}

.download-img {
  background-image: url(/odin-jollibee-clone/static/media/download.97aff226.png);
}

.call-img {
  background-image: url(/odin-jollibee-clone/static/media/call.88c6db6a.png);
}

hr {
  width: min(1100px, 90%);
  margin: 0 auto;
}

/* EXCLUSIVE PROMOS WITH SOME FEATURED SECTION*/

.promos__container,
.featured__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;

  padding: 2em 0;
}

.promos__container h2,
.featured__container h2 {
  font-size: 2em;
}

.promos__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1em;
  gap: 1em;
}

.promos__cards .card {
  width: min(327px, 30vw);
  aspect-ratio: 0.8;
  background-size: cover;
  border-radius: 30px;
  box-shadow: 0 0 10px 1px rgb(0 0 0/ 0.4);
  cursor: pointer;
}

.promos__cards .card:hover {
  scale: 1.01;
}

.funko-card {
  background-image: url(/odin-jollibee-clone/static/media/promos-funko.04f677c0.jpg);
}

.freebie-card {
  background-image: url(/odin-jollibee-clone/static/media/promo-mid.2e5a20f4.png);
}

.tenten-card {
  background-image: url(/odin-jollibee-clone/static/media/promos-last.aba79432.jpg);
}

.promos__container button {
  background-color: var(--clr-secondary);
  border: none;
  font-family: var(--font-bold);
  color: white;
  padding: 0.8em 1.6em;
  border-radius: 99em;
  font-size: 1.5em;
  box-shadow: 0 0 10px 1px rgb(0 0 0/ 0.4);
  cursor: pointer;
}

.promos__container button:hover {
  background-color: var(--clr-hover);
}

/* FEATURED SECTION */

.featured {
  background-color: var(--clr-bg-secondary);
}

.featured__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5em;
  gap: 1.5em;
}

.featured .card {
  width: min(380px, 30vw);
  border-radius: 30px;
  padding: 1.5em 1.5em 0;
  color: white;
  line-height: 1.4;

  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
}

.featured .card:hover {
  scale: 1.01;
}

.featured .card h4 {
  font-size: 2em;
}

.featured .card p {
  font-size: 1.4em;
  font-family: var(--font-thin);
  font-weight: bold;
}

.featured .card img {
  margin: auto auto 0;
}

.chicken-card {
  background-color: #d6001c;
}

.spag-card {
  background-color: #ffa400;
}

.burger-card {
  background-color: var(--clr-accent);
}

.featured__container button {
  background-color: var(--clr-secondary);
  border: none;
  font-family: var(--font-bold);
  color: white;
  padding: 0.8em 1.6em;
  border-radius: 99em;
  font-size: 1.5em;
  box-shadow: 0 0 10px 1px rgb(0 0 0/ 0.4);
  cursor: pointer;
}

.featured__container button:hover {
  background-color: var(--clr-hover);
}

/* WHO WE ARE SECTION*/

.who-we-are__container {
  display: flex;
  justify-content: center;
  padding: 2.5em 1em;
  grid-gap: 10vw;
  gap: 10vw;
}

.what-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1.5em;
  gap: 1.5em;
}

.what-section h4 {
  font-size: 2em;
  padding-top: 0.5em;
}

.what-we-do hr {
  width: 60%;
  border-bottom: none;
  border: 3px solid var(--clr-secondary);
}

.what-we-stand hr {
  width: 80%;
  border-bottom: none;
  border: 3px solid var(--clr-primary);
}

.what-section p {
  width: 40ch;
  font-family: var(--font-thin);
  text-align: center;
  line-height: 1.4;
}

.what-section .img {
  border-radius: 20px;
  width: 220px;
  height: 220px;
  aspect-ratio: 1;
  position: relative;
}

.what-section .img::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  border-radius: 20px;
}

.what-we-do-img {
  background-image: url(/odin-jollibee-clone/static/media/what-wedo.d7c4a37a.jpeg);
}

.what-we-do-img::before {
  top: 20px;
  left: 35px;
  background-color: var(--clr-primary);
}

.what-we-stand-img {
  background-image: url(/odin-jollibee-clone/static/media/what-we-stand.fb3e80a7.jpeg);
}

.what-we-stand-img::before {
  top: 20px;
  left: -35px;
  background-color: var(--clr-secondary);
}

.footer {
  border-top: 3px solid var(--clr-secondary);
}

.footer__container {
  padding: 2em 1em 0;
  display: grid;
  grid-gap: 1em;
  gap: 1em;
}

/* SECTION 1 */
.section1 {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.section1 .logo svg {
  width: 150px;
}

.section1 .order-btn {
  justify-self: end;
  align-self: flex-end;
}

/* SECTION 2 */

.section2 {
  display: flex;
  justify-content: space-between;
  grid-gap: 1em;
  gap: 1em;
}

/* SECTION 2 ---- Accounts */

.accts {
  display: grid;
  grid-gap: 1em;
  gap: 1em;
  align-self: flex-start;
}

/* all h5 */
h5 {
  font-size: 1.3em;
}

.icons {
  display: flex;
  grid-gap: 0.5em;
  gap: 0.5em;
}

.icons svg {
  width: 30px;
  fill: var(--clr-primary);
  cursor: pointer;
}

.icons svg:hover {
  scale: 1.1;
  position: relative;
  bottom: 0.5em;
}

/* SECTION 2 ---- Footer Nav */

.footer-nav {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1em;
  gap: 1em;
}

.footer-nav ul {
  padding-top: 0.5em;
  display: grid;
  grid-gap: 0.5em;
  gap: 0.5em;
}

.footer-nav li {
  text-decoration: none;
  list-style-type: none;
  font-family: var(--font-primary);
  cursor: pointer;
}

.footer-nav li:hover {
  text-decoration: underline;
}

/* SECTION 2 ---- Download Button */

.download-btns {
  display: grid;
  grid-gap: 1em;
  gap: 1em;
  align-self: flex-start;
}

.download-btn {
  width: 16ch;
  background-color: hsl(0, 0%, 5%);
  padding: 0.4em;
  border-radius: 10px;
  cursor: pointer;

  box-shadow: 0 0 10px 0px rgb(13, 13, 13, 0.4);

  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 0.5em;
  gap: 0.5em;
}

.download-btn:hover {
  scale: 1.05;
}

.download-btn .btn-cta {
  display: grid;
  grid-gap: 0;
  gap: 0;
}

.download-btn .btn-cta p {
  color: white;
}

.download-btn .btn-cta p:first-child {
  font-family: sans-serif;
  font-size: 0.8em;
}

.download-btn .btn-cta p:last-child {
  font-family: sans-serif;
  font-weight: 700;
  font-size: 1.1em;
}
.download-btns svg {
  width: 32px;
  fill: white;
}

.copyright {
  text-align: center;
  font-family: var(--font-primary);
  margin-top: 1em;
}

.delivery__hero__container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  grid-gap: 2em;
  gap: 2em;

  padding: 6em 1em 5em;
}

.delivery__hero__img {
  position: relative;
}

.delivery__hero__img img {
  width: 450px;
}

.delivery__hero__img:before {
  content: "";
  position: absolute;
  left: -4rem;
  top: 2rem;
  background-color: var(--clr-secondary);
  width: 100%;
  height: 100%;
  border-radius: 1.5rem;
  z-index: -1;
}

.delivery__hero__content {
  width: 50ch;

  display: grid;
  justify-items: start;
  grid-gap: 2em;
  gap: 2em;
}

.delivery__hero__content h3 {
  color: var(--clr-primary);
  font-size: 2.1em;
}

.delivery__hero__content p {
  font-family: var(--font-thin);
  line-height: 2;
  font-size: 1.1em;
}

.delivery__hero__content button {
  font-size: 25px;
  padding: 15px 30px;
  border-radius: 0.825em;
}

/* DELIVERY CARDS SECTION */

.delivery__cards-section {
  background: #ffeab8;
}

.delivery__cards__container {
  padding: 2.5em 1em;

  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(375px, 50%), 1fr));
  grid-gap: 1em;
  gap: 1em;
}

.delivery__card {
  background-color: white;
  padding: 2em 1em;

  display: grid;
  justify-items: center;
  align-items: center;
  grid-gap: 2em;
  gap: 2em;

  border-radius: 1.5em;
  box-shadow: 0 0 1em 0 rgb(0 0 0 / 0.2);
}

.delivery__card .card-img {
  height: 125px;
}

.delivery__card h3 {
  font-size: 2em;
}

.delivery__card p:not(.download-btns p) {
  text-align: center;
  line-height: 2;
  font-family: var(--font-primary);
  font-size: 1.2em;
}

.delivery__card .download-btns {
  display: flex;
}

/* DELIVERY FAQs */

.delivery__faq__container {
  padding: 4em 0;
}

.delivery__faq__container h2 {
  font-size: 2em;
  text-align: center;
}

.FAQs {
  --answer-open-close-speed: 200ms;

  width: min(1250px, 97%);
  margin: 2em auto 0;
  display: grid;
  grid-gap: 1em;
  gap: 1em;
}

.FAQs__question {
  font-size: 1.5em;
  box-shadow: 0 0 2em -0.5em rgb(0 0 0 / 0.3);
  padding: 1.3em 1.5em 1.1em;
  border-radius: 2em;
  cursor: pointer;
}

.FAQs__answer {
  box-shadow: 0 0 2em -0.5em rgb(0 0 0 / 0.3);

  padding: 2em 2em 2em 4em;
  border-radius: 2em;
  line-height: 2;
  font-family: var(--font-primary);
  font-size: 1.2em;
}

.FAQs__answer li {
  list-style-position: inside;
}

.FAQs__answer > * {
  display: none;
}

.FAQs__answer__close {
  -webkit-animation: close var(--answer-open-close-speed) ease forwards;
          animation: close var(--answer-open-close-speed) ease forwards;
}

@-webkit-keyframes close {
  0% {
    height: 100%;
    overflow: hidden;
  }
  100% {
    height: 0;
    overflow: hidden;
    padding-top: 0;
    padding-bottom: 0;
  }
}

@keyframes close {
  0% {
    height: 100%;
    overflow: hidden;
  }
  100% {
    height: 0;
    overflow: hidden;
    padding-top: 0;
    padding-bottom: 0;
  }
}

.FAQs__answer__open {
  -webkit-animation: open var(--answer-open-close-speed) ease forwards;
          animation: open var(--answer-open-close-speed) ease forwards;
}

@-webkit-keyframes open {
  0% {
    height: 0;
    overflow: hidden;
    padding-top: 0;
    padding-bottom: 0;
  }
  100% {
    height: 100%;
    overflow: hidden;
  }
}

@keyframes open {
  0% {
    height: 0;
    overflow: hidden;
    padding-top: 0;
    padding-bottom: 0;
  }
  100% {
    height: 100%;
    overflow: hidden;
  }
}

.page__header__title {
  font-size: 2.5em;
  text-align: center;
  background: var(--clr-primary);
  color: white;
  padding: 0.75em 0;
}

.jollibee__promos__container {
  text-align: center;
  display: grid;
  grid-gap: 1.5em;
  gap: 1.5em;

  padding: 2em 1em;
}

.jollibee__promos__container h3 {
  font-size: 2.3em;
  color: var(--clr-primary);
}
.jollibee__promos__container p {
  font-size: 1.1em;
  font-family: var(--font-thin);
  line-height: 2;
}

/* Promos Cards */

.jollibee__promos__cards__section {
  background-color: #fff1d0;
}

.jollibee__promos__cards__container {
  padding: 4em 1em;
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(auto-fill, minmax(min(400px, 50%), 1fr));
  grid-gap: 4em;
  gap: 4em;
}

.promotion__card {
  max-width: 400px;
  display: grid;
  justify-items: center;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
}

.promotion__card img {
  max-width: 400px;
  border-radius: 2em;
  box-shadow: 0 0 1em 0 rgb(0 0 0/ 0.4);
}

.promotion__description {
  text-align: center;
  font-size: clamp(1.2em, 5vw, 2em);
}

.promotions__action__buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 2em;
  gap: 2em;
}

.promotions__action__buttons .link {
  color: #fa8003;
  font-size: 1.2em;
}

.promotions__action__buttons .link:hover {
  color: rgba(250, 128, 3, 0.7);
}

